.avisos-container {
    padding-top: env(safe-area-inset-top);
    background: linear-gradient(to bottom, #0e2a3b, #152f40);
    min-height: 100vh;
    color: #e8e8e8;
    box-sizing: border-box;
    font-family: Arial, sans-serif;
  }
  
  .avisos-header {
    display: flex;
    align-items: center;
    justify-content: space-between; /* espalha bot√£o, t√≠tulo e placeholder */
    position: sticky;
    top: 0;
    z-index: 100;
    padding: 15px;
    background: linear-gradient(to bottom, #3a5568, #152f40);
    border-bottom: 1px solid #3a5568;
    margin-bottom: 20px;
    border-radius: 0;
  }
  
  .placeholder {
    width: 40px; /* largura aproximada do bot√£o */
    margin-left: 15px; /* igual nos dois */
  }
  
  .avisos-back-button {
    padding: 0;
    font-size: 1.4rem;
    margin-right: 15px; /* igual nos dois */
    cursor: pointer;
    background: none;
    border: none;
    color: #e8e8e8;
    display: flex;
    align-items: center;
    justify-content: center;
    transition: color 0.2s ease;
    width: 40px; /* fixa largura */
    height: 24px; /* fixa altura */
  }
  
  .avisos-back-button:hover {
    color: #ffffff;
  }
  
  .avisos-title {
    font-size: 18px;
    font-weight: normal;
    margin: 0;
    color: #ffffff;
    text-align: center;
    flex-grow: 0;
    flex-shrink: 0;
    max-width: 200px; /* evita que o t√≠tulo fique muito largo */
  }
  
  .avisos-list {
    display: flex;
    flex-direction: column;
    gap: 12px;
    padding: 0 20px;
  }
  
  .avisos-item {
    background-color: #1e3a4c;
    border-radius: 12px;
    padding: 16px;
    box-shadow: 0 2px 5px rgba(0, 0, 0, 0.2);
    transition: transform 0.2s;
  }
  
  .avisos-item:hover {
    transform: scale(1.02);
  }
  
  .avisos-item h3 {
    margin: 0;
    font-size: 14px;
    color: #d1d5db;
  }
  